@import "https://rsms.me/inter/inter-ui.css";
*, :after, :before {
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
}

body {
  height: 100vh;
  min-height: 100%;
  min-width: 100%;
  background-size: cover;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: dunbar-tall, sans-serif;
  font-size: medium;
  font-style: normal;
  overflow-y: hidden;
}

body.bg-dark {
  background-image: url("desert-dark.1297c21b.svg");
}

body.bg-light {
  background-image: url("desert-light.db46f69f.svg");
}

body a {
  color: inherit;
  text-decoration: none;
}

body button.btn-primary, body h1 {
  width: -moz-fit-content;
  width: fit-content;
}

body button.btn-primary:before, body h1:before {
  transform-origin: 100% 100%;
  transform: scaleX(0);
}

body button.btn-primary:hover:before, body h1:hover:before {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

body button.btn-primary:before, body h1:before {
  content: " ";
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background: #9df;
  transition: transform .3s;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

body header, body header .navbar, body header .navbar .container-fluid, body main, body aside, body footer {
  border-radius: .5rem;
}

body header {
  z-index: 1;
  width: calc(100% - 1rem);
  height: calc(7.75% - 1rem);
  margin: .5rem;
}

body header .logo {
  font-size: 3rem;
  transition: all 3s;
}

body header:hover .logo {
  transform: rotate(1080deg);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

body header .dropdown-menu.show {
  animation: .3s alternate fadeIn;
}

body header .nav-item.dropdown.dropdown-mega {
  position: static;
}

body header .nav-item.dropdown.dropdown-mega .dropdown-menu {
  width: 90%;
  top: auto;
  left: 5%;
}

body header .navbar .container-fluid {
  background: inherit;
}

@media (max-width: 991px) {
  body header .navbar .container-fluid .navbar-collapse {
    margin-bottom: .75rem;
  }
}

body header .navbar-toggler {
  border: none;
  outline: none;
  padding: 0;
}

body header .navbar-toggler:focus {
  box-shadow: none;
}

body header .navbar-toggler .hamburger-toggle {
  z-index: 11;
  float: right;
  width: 50px;
  height: 50px;
  display: inline-block;
  position: relative;
}

body header .navbar-toggler .hamburger-toggle .hamburger {
  pointer-events: none;
  width: 50%;
  height: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(0);
}

body header .navbar-toggler .hamburger-toggle .hamburger span {
  z-index: 1;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  transition: transform .2s cubic-bezier(.77, .2, .05, 1), background .2s cubic-bezier(.77, .2, .05, 1), all .2s ease-in-out;
  position: absolute;
  left: 0;
}

body header .navbar-toggler .hamburger-toggle .hamburger span:first-child {
  transform-origin: 50%;
  top: 10%;
  transform: translate(0% -50%) !important;
}

body header .navbar-toggler .hamburger-toggle .hamburger span:nth-child(2) {
  top: 50%;
  transform: translate(0, -50%);
}

body header .navbar-toggler .hamburger-toggle .hamburger span:last-child {
  transform-origin: 50%;
  top: auto;
  bottom: 10%;
  left: 0;
}

body header .navbar-toggler .hamburger-toggle .hamburger.active span {
  margin: 0;
  position: absolute;
}

body header .navbar-toggler .hamburger-toggle .hamburger.active span:first-child {
  top: 45%;
  transform: rotate(45deg);
}

body header .navbar-toggler .hamburger-toggle .hamburger.active span:nth-child(2) {
  width: 0;
  left: 50%;
}

body header .navbar-toggler .hamburger-toggle .hamburger.active span:last-child {
  top: 45%;
  transform: rotate(-45deg);
}

body main {
  width: calc(100% - 1rem);
  height: 88.5%;
  margin: 0 .5rem;
  overflow-y: auto;
}

body aside {
  display: none;
}

body footer {
  width: calc(100% - 1rem);
  height: calc(3.75% - 1rem);
  margin: .5rem;
}

@media (min-width: 992px) {
  body main {
    width: calc(80% - 1rem);
  }

  body aside {
    width: calc(20% - .5rem);
    height: 88.5%;
    display: block;
    overflow-y: auto;
  }
}

